import { gsap, Back, Elastic, Expo } from 'gsap';

export default {
  mounted() {
    this.animateHero();
  },
  methods: {
    animateHero() {
      const { talk, talkPulse } = this.$refs;
      const tl = gsap.timeline();

      tl.to(
        '.callout',
        {
          scale: 1,
          duration: 1,
          ease: 'elastic.out(1, 0.3)'
        },
        0.5
      )
        .to('.letter', { fontSize: '3rem', duration: 0.05, stagger: 0.1 })
        .to('.des', { opacity: 1, y: 0, duration: 0.7 }, 4)
        .to(
          '.message',
          { opacity: 1, y: 0, duration: 1, ease: 'back.out(1.7)' },
          '-=.5'
        )
        .to('.talk-wrapper', { opacity: 1 })
        .to(
          talk,
          {
            duration: 0.4,
            scale: 0.8,
            rotation: 16,
            ease: Back.easeOut.config(1.7)
          },
          5
        )
        .to(
          talkPulse,
          {
            duration: 0.5,
            scale: 0.9,
            opacity: 1
          },
          '-=0.6'
        )
        .to(talk, {
          duration: 1.2,
          scale: 1,
          rotation: '-=16',
          ease: Elastic.easeOut.config(2.5, 0.5)
        })
        .to(
          talkPulse,
          {
            duration: 1.1,
            scale: 3,
            opacity: 0,
            ease: Expo.easeOut
          },
          '-=1.2'
        );
    }
  }
};
